@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about{background:#1c2022;color:#fff}.about__body{width:96%;display:flex;max-width:1020px}.about__pic{width:20%;margin:1rem;display:flex;align-items:center}.about__pic img{width:120px;height:120px;border-radius:100%}.about__desc{margin:2rem 1rem;width:80%;display:flex;flex-wrap:wrap;color:#7A7A7A}.about__desc h2,.about__desc p{width:100%;margin-top:0.5rem}.about__desc h2{margin:0;color:#fff;font-size:1.25rem;font-weight:600;text-transform:uppercase}.about__desc-location{width:50%}.about__desc-download{width:50%;display:flex;justify-content:center;align-items:center}@media screen and (max-width: 767px){.about__body{flex-direction:column}.about__pic{width:100%;justify-content:center;margin:0;margin-top:.5rem}.about__pic img{width:150px;height:150px}.about__desc{margin:0;padding:1rem;width:90%;flex-direction:column}.about__desc-location{width:100%}.about__desc-download{margin-top:1rem;width:100%}}

.top-nav{position:fixed;top:0;width:100%;display:flex;justify-content:center}.top-nav>a{padding:1rem;font-size:1.2rem;font-weight:600;color:#fff;text-decoration:none;text-transform:uppercase;mix-blend-mode:difference}.top-nav>a:hover{color:rgba(43,73,255,0.7)}.top-nav .active{color:#2b49ff}

.socials{font-size:2.5rem}.socials>a>svg{margin:0.5rem;color:#e6e6e6}.socials>a>svg:hover{color:#2b49ff}@media screen and (max-width: 960px){.socials{margin-top:.5rem}}

.hero{width:100%;height:100vh;display:flex;flex-direction:column;align-items:center;color:#fff;background-image:linear-gradient(45deg, gray 25%, transparent 25%),linear-gradient(-45deg, gray 25%, transparent 25%),linear-gradient(45deg, transparent 75%, gray 75%),linear-gradient(-45deg, transparent 75%, gray 75%);background-size:200px 200px;background-position:bottom center}.hero__heading{display:flex;align-items:center;flex-direction:column;justify-content:center;margin-top:auto;height:auto;text-align:center}.hero__heading>h1{font-size:5rem;text-shadow:0px 1px 3px rgba(0,0,0,0.8);margin:0}.hero__heading>h2{color:#e6e6e6;text-shadow:0px 1px 3px rgba(0,0,0,0.8)}.hero__arrow{font-size:3.5rem;margin-top:auto}.hero__arrow:hover{color:#2b49ff}@media screen and (max-width: 960px){.hero{background-size:100px 100px}.hero__heading>h1{font-size:4rem}}

.resume{background:#fff}.resume__body{width:96%;display:flex;max-width:1020px;padding-bottom:1.5rem;border-bottom:1px solid lightgrey}.resume__body:last-child{border:none}.resume__heading{width:20%;display:flex;margin:1rem;padding-top:0.25rem}.resume__heading>h2{font-weight:600;text-transform:uppercase}.resume__heading>h2>span{border-bottom:3px solid #2b49ff}.resume__skills{width:80%;margin:1rem}.resume__skills>p{color:#7A7A7A;margin:0.5rem 0}.resume__skills>p:first-of-type{padding-top:1.5rem}.resume__desc{width:80%;margin:1rem}.resume__desc>h3{font-size:1.5rem;font-weight:600;margin-bottom:0}.resume__desc>p{color:#7A7A7A;margin:0.5rem 0}@media screen and (max-width: 767px){.resume__body{flex-direction:column;align-items:center}.resume__heading{width:auto;margin:0}.resume__skills{margin:0}.resume__desc{margin:0}}

.projects{background:gray}.projects__body{display:flex;flex-wrap:wrap}.projects__card{width:300px;margin:1rem;position:relative;background-color:#fff;border-radius:0.5rem;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);-webkit-transition:0.3s;transition:0.3s}.projects__card img{width:100%;height:100%;border-radius:0.5rem}.projects__card:hover{box-shadow:0 8px 16px 0 rgba(0,0,0,0.2)}.projects__card:hover .projects__card__overlay{opacity:.9}.projects__card__overlay{height:auto;width:auto;position:absolute;padding:1rem;background-color:#2b49ff;border-radius:0.5rem;top:0;bottom:0;left:0;right:0;opacity:0;-webkit-transition:0.5s ease;transition:0.5s ease;display:flex;align-items:center;justify-content:center;flex-direction:column;padding:auto}.projects__card__overlay a{font-size:2rem;font-weight:800;text-align:center;text-decoration:none;color:white}

.form{display:flex;flex-direction:column;align-items:flex-end;color:#fff}.form>.button{margin-top:0.5rem}.form__email,.form__name,.form__textbox{width:500px;max-width:96%}.form__email>h2,.form__name>h2,.form__textbox>h2{margin:0.5rem 0;font-size:1.25rem;font-weight:600;text-transform:uppercase}.form__email>input,.form__email>textarea,.form__name>input,.form__name>textarea,.form__textbox>input,.form__textbox>textarea{font-size:1rem;width:100%;border:none;border-radius:5px}.form__email>input::-webkit-input-placeholder, .form__email>textarea::-webkit-input-placeholder, .form__name>input::-webkit-input-placeholder, .form__name>textarea::-webkit-input-placeholder, .form__textbox>input::-webkit-input-placeholder, .form__textbox>textarea::-webkit-input-placeholder{color:#e6e6e6;opacity:1}.form__email>input:-ms-input-placeholder, .form__email>textarea:-ms-input-placeholder, .form__name>input:-ms-input-placeholder, .form__name>textarea:-ms-input-placeholder, .form__textbox>input:-ms-input-placeholder, .form__textbox>textarea:-ms-input-placeholder{color:#e6e6e6;opacity:1}.form__email>input::-ms-input-placeholder, .form__email>textarea::-ms-input-placeholder, .form__name>input::-ms-input-placeholder, .form__name>textarea::-ms-input-placeholder, .form__textbox>input::-ms-input-placeholder, .form__textbox>textarea::-ms-input-placeholder{color:#e6e6e6;opacity:1}.form__email>input::placeholder,.form__email>textarea::placeholder,.form__name>input::placeholder,.form__name>textarea::placeholder,.form__textbox>input::placeholder,.form__textbox>textarea::placeholder{color:#e6e6e6;opacity:1}.form__email>input,.form__name>input,.form__textbox>input{line-height:2rem}.form__email>textarea,.form__name>textarea,.form__textbox>textarea{font-family:inherit;height:9rem}@media screen and (max-width: 960px){.form{width:100%;align-items:center}.form>.button{width:50%}.form__email,.form__name,.form__textbox{width:80%}}

.contact.container{padding:4rem 0}

body{font-family:"Open Sans", sans-serif;background:#1c2022}.App{display:flex;flex-direction:column;align-items:center}.container{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:2rem 0}.button{background-color:#2b49ff;text-decoration:none;color:#fff;font-size:1rem;font-weight:600;padding:0.75rem 1.5rem;border-radius:5px;border:none}.button>svg{font-size:1.25rem;margin-right:0.5rem}.button:hover{background-color:rgba(43,73,255,0.7)}@media screen and (max-width: 767px){.container{padding:1rem 0}}

